// pages/index.js
import { useSelector, useDispatch } from "react-redux";
import { addToCart, updateQuantity } from "../store/cartSlice";

export default function Home({ products }) {
  const cart = useSelector((state) => state.cart);
  const dispatch = useDispatch();

  const totalItems = Object.values(cart).reduce((sum, qty) => sum + qty, 0);
  const totalPrice = Object.entries(cart).reduce((sum, [id, qty]) => {
    const product = products.find((p) => p.id === parseInt(id));
    return sum + (product?.price || 0) * qty;
  }, 0);

  return (
    <div className="min-h-screen bg-gray-50 px-6 py-10">
      <header className="flex justify-between items-center mb-10">
        <h1 className="text-3xl font-extrabold text-gray-900">üõçÔ∏è Trendy Store</h1>
        <div className="text-lg font-medium text-gray-700 bg-white px-4 py-2 rounded shadow">
          Cart: {totalItems} items ‚Äì ${totalPrice.toFixed(2)}
        </div>
      </header>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8">
        {products.map((product) => (
          <div
            key={product.id}
            className="bg-white p-6 rounded-xl shadow-md hover:shadow-lg transition duration-300"
          >
            <div className="mb-4">
              <h2 className="text-xl font-semibold text-gray-800">{product.name}</h2>
              <p className="text-gray-600">${product.price}</p>
            </div>

            <button
              className="w-full bg-indigo-600 text-white py-2 px-4 rounded hover:bg-indigo-700 transition"
              onClick={() => dispatch(addToCart(product.id))}
            >
              Add to Cart
            </button>

            {cart[product.id] && (
              <div className="mt-4">
                <label className="block text-sm text-gray-600 mb-1">Quantity:</label>
                <input
                  type="number"
                  value={cart[product.id]}
                  onChange={(e) =>
                    dispatch(
                      updateQuantity({
                        id: product.id,
                        quantity: parseInt(e.target.value) || 0,
                      })
                    )
                  }
                  className="w-full border border-gray-300 rounded px-3 py-1 focus:ring-2 focus:ring-indigo-500 outline-none"
                />
              </div>
            )}
          </div>
        ))}
      </div>
    </div>
  );
}

export async function getServerSideProps() {
  const mockProducts = [
    { id: 1, name: "Classic T-Shirt", price: 20 },
    { id: 2, name: "Stylish Shoes", price: 50 },
    { id: 3, name: "Elegant Hat", price: 15 },
  ];
  return {
    props: { products: mockProducts },
  };
}
